@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;1,700&family=Orbitron:wght@700&display=swap);
body {
  margin: 0;
}

.App{font-family:"Open Sans", sans-serif;display:flex;justify-content:center;align-items:center;height:100vh;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#drum-machine{display:flex;flex-direction:column;justify-content:space-evenly;color:#e0e0e0;border-radius:7px;max-width:450px;text-align:center;padding:10px;margin:10px;background:#151415;background:-webkit-gradient(left top, left bottom, color-stop(0%, #151415), color-stop(100%, #4a4a4a));background:linear-gradient(to bottom, #151415 0%, #4a4a4a 100%);filter:progid:DXImageTransform.Microsoft.gradient( startColorstr='#151415', endColorstr='#4a4a4a', GradientType=0 )}#ui-container{display:flex;justify-content:space-between;align-items:center;height:150px}#drum-pads-container{background-color:#292828;border:1px solid rgba(255,255,255,0.5);border-radius:7px;padding:5px;display:flex;flex-wrap:wrap;justify-content:space-evenly}

#title{font-style:italic;font-weight:bold;font-size:1.1em;width:30%;height:75%;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.title-icon{color:brown;font-size:1.7em}#display{font-family:"Orbitron", sans-serif;background-color:brown;border:3px solid black;border-radius:7px;color:black;height:75%;width:40%;margin:10px;display:flex;flex-direction:column;justify-content:space-around}.status{display:flex;justify-content:space-around}#controls{width:30%;display:flex;justify-content:space-around}.controls-volume{display:flex;flex-direction:column;justify-content:center}.slider-wrapper input{width:40px;height:100px;-webkit-appearance:none;-webkit-appearance:slider-vertical}.slider-wrapper input:hover{cursor:pointer}.controls-power{display:flex;flex-direction:column;justify-content:center}.btn-wrapper:hover{cursor:pointer}.pwr-btn-on{color:brown;font-size:2em}.pwr-btn-off{color:white;font-size:2em}

.drum-pad{background-color:rgba(54,52,52,0.8);border:1px solid rgba(165,42,42,0.3);border-radius:7px;box-shadow:0px 1px 8px -3px brown;box-sizing:border-box;flex:0 1 31%;margin:1%;height:80px;width:80px;display:flex;justify-content:center;align-items:center}.drum-pad:hover{cursor:pointer}

