.drum-pad {
  background-color: rgba(54, 52, 52, 0.8);
  border: 1px solid rgba(165, 42, 42, 0.3);
  border-radius: 7px;
  -webkit-box-shadow: 0px 1px 8px -3px rgba(165, 42, 42, 1);
  -moz-box-shadow: 0px 1px 8px -3px rgba(165, 42, 42, 1);
  box-shadow: 0px 1px 8px -3px rgba(165, 42, 42, 1);
  box-sizing: border-box;
  flex: 0 31%;
  margin: 1%;
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.drum-pad:hover {
  cursor: pointer;
}
